import React, { Component } from 'react'
import reactDOM from 'react-dom'
import '../../../src/style.css'

class SearchInput extends Component {
    constructor(props) {//
        super(props);
        this.state = { searchInput: '' };//
        this.search = this.search.bind(this);//
        this.handleChange = this.handleChange.bind(this);
        this.searchTimer = null;//
    }
    //
    handleChange(event) {
        this.setState({
            searchInput: event.target.value
        }, () => {
            var that = this;
            if (that.searchTimer) clearTimeout(that.searchTimer);
            that.searchTimer = setTimeout(() => {
                that.search();
            }, 500);
        });
    }
    search() {
        // console.log(this.props)
        this.props.search(this.state.searchInput);
    }
    render() {
        let placeholder;
        const seartInput = this.props.seartInput ? '' : this.props.seartInput;
        switch (this.props.page) {
            case 'book':
                placeholder = 'ISBN';
                break;
            case 'film':
                placeholder = '';
                break;
            case 'music':
                placeholder = '';
                break;
        }
        return (
            <div className="search-bar">
                <input type="text" placeholder={placeholder} value={seartInput} onChange={this.handleChange} />
                <button onClick={this.search}></button>
            </div>

        )
    }
}
module.exports = SearchInput;